.input{
    border-radius: 4px;
    background-color: white;
    padding: 3px;
}

.result{
    padding: 3px;
    border-radius: 4px;
    cursor: default;
}

.btn{
    background-color: green;
    width: 100%;
    padding-top:3px ;
    padding-bottom:3px ;
    border-style: none;
    border-radius: 4px;
    cursor: pointer;
    color: white;
    font-weight: bold;
    text-transform: uppercase;
    transition: 0.5s;   
}

.btn:hover{
    background-color: lightgreen;
}