.btn{
    border-style: none;
    padding: 4px 6px 4px 6px;
    margin-bottom: 15px;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    transition: 0.25s;
    background-color: white;
    border-radius: 4px;
    margin: 2px;
    
}

.btn:hover{
    background-color: #61dafb;
    color: white;
}